.footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #67b35c;
  padding: 20px 40px;
  margin-top: 20px;

  .logo {
    width: 70px;
    height: 70px;
    object-fit: contain;
  }

  .social-wrapper {
    display: flex;

    .icon-wrapper {
      background-color: white;
      padding: 10px;
      border-radius: 40px;
      margin: 0 10px;

      .icon-medsos {
        width: 30px;
        height: 30px;
        object-fit: contain;
      }
    }
  }
}

.copyright {
  background-color: #569e46;
  padding: 10px 0;

  p {
    font-size: 14px;
    color: white;
    text-align: center;
    margin: 0;
  }
}
